import React from 'react';
import { Carousel } from 'react-bootstrap'
import { Link } from 'react-scroll'

const Main = () =>{

    return(
        <div>
           
            <header id="mu-hero main" style={{backgroundColor: "#23960b"}} >
                <div class="container" >
                    <nav class="navbar navbar-expand-lg navbar-light mu-navbar" >
                    
                        
                        <Link to="main">
                            <a class="navbar-brand mu-logo" href="index.html">
                                <img src="assets/images/logo2.png" alt="logo"  width="40" height="40" class="d-inline-block align-center" alt="" /></a> 
                    
                        </Link>
                        
                        <a class="navbar-brand mu-logo" href="index.html"><span><h3>Prithvi CleanTech</h3></span></a>
                        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span class="fa fa-bars"></span>
                        </button>

                        <div class="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul class="navbar-nav mr-auto mu-navbar-nav">
                                <Link to="home">
                                    <li class="nav-item active">
                                        <a href="index.html">Home</a>
                                        </li>
                                </Link>
                                
                                <Link to ="mu-about" smooth = {true}><li class="nav-item"><a href="about-us.html">About us</a></li></Link>
                                <Link to ="service" smooth = {true}><li class="nav-item"><a href="services.html">Services</a></li></Link>
                                <Link to ="projects" smooth = {true}><li class="nav-item"><a href="portfolio.html">Portfolio</a></li></Link>
                                <li class="nav-item dropdown">
                                        <a class="dropdown-toggle" href="blog.html" role="button" id="navbarDropdown" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Blog</a>
                                    <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <Link to ="blog" smooth = {true}  >  <a class="dropdown-item" href="blog.html">Blog Archive</a></Link>
                                    <Link to ="blog" smooth = {true} ><a class="dropdown-item" href="blog-single.html">Blog Single</a></Link>
                                        </div>
                                    </li>
                                    <Link to="contact" smooth={true}><li class="nav-item"><a href="contact.html">Contact us</a></li></Link>
                            
                            </ul>
                        </div>
                        
                    </nav>
                </div>
	        </header>

            
            
        </div>
    )
}

export default Main;