import React from 'react';
import { Carousel } from 'react-bootstrap'

const Home = () =>
{
    return(
            <div style = {{backgroundColor:"#23960b"}} >
                <h3 style={{color:"white"}}> Go Green Go Clean</h3>
                    
                    <Carousel >
                        <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src="assets/images/crousel1.jpg" 
                            alt="First slide"
                            />
                            <Carousel.Caption>
                            <h3>First slide label</h3>
                            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                            <a class="mu-primary-btn" href="#">Read more <span class="fa fa-long-arrow-right"></span></a>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src="assets/images/crousel2.jpg"
                            alt="Third slide"
                            />

                            <Carousel.Caption>
                            <h3>Second slide label</h3>
                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                            </Carousel.Caption>
                        </Carousel.Item> 

                    </Carousel>
            </div>
        )
}

export default Home;